import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'searchFilter'
})
export class SearchPipe implements PipeTransform {

 
  transform(users: any, search: any): any {
    if(!users || !search){
      return users;
    }
    return users.filter(users=>
      users.CIFNumber.indexOf(search)!== -1);
    
  }
}
